/**
 * Service d'indexation et de recherche en mmoire
 * Utilis comme fallback lorsque Elasticsearch n'est pas disponible
 */

class InMemoryIndex {
  constructor() {
    this.items = [];
    this.lastUpdate = null;
  }

  /**
   * Ajoute un lment  l'index
   * @param {Object} item - lment  ajouter
   */
  addItem(item) {
    // Vrifier si l'lment existe dj (par titre)
    const existingIndex = this.items.findIndex(
      existing => existing.title === item.title && existing.source === item.source
    );

    if (existingIndex !== -1) {
      // Mettre  jour l'lment existant
      this.items[existingIndex] = {
        ...this.items[existingIndex],
        ...item,
        updated_at: new Date()
      };
    } else {
      // Ajouter un nouvel lment
      this.items.push({
        ...item,
        created_at: item.created_at || new Date(),
        updated_at: new Date()
      });
    }

    this.lastUpdate = new Date();
  }

  /**
   * Supprime un lment de l'index
   * @param {String} title - Titre de l'lment  supprimer
   * @param {String} source - Source de l'lment  supprimer
   * @returns {Boolean} True si l'lment a t supprim
   */
  removeItem(title, source) {
    const initialLength = this.items.length;
    this.items = this.items.filter(
      item => !(item.title === title && item.source === source)
    );
    
    if (this.items.length !== initialLength) {
      this.lastUpdate = new Date();
      return true;
    }
    
    return false;
  }

  /**
   * Vide l'index
   */
  clear() {
    this.items = [];
    this.lastUpdate = new Date();
  }

  /**
   * Recherche dans l'index
   * @param {String} query - Terme de recherche
   * @returns {Array} Rsultats de recherche
   */
  search(query) {
    if (!query || query.trim() === '') {
      return [];
    }

    // Normaliser la requte pour la recherche
    const normalizedQuery = query.toLowerCase().trim();
    const queryTerms = normalizedQuery.split(/\s+/);

    // Fonction pour calculer un score de pertinence simple
    const calculateScore = (item) => {
      let score = 0;
      
      // Vrifier le titre (poids plus lev)
      const title = (item.title || '').toLowerCase();
      if (title.includes(normalizedQuery)) {
        score += 10;
      }
      
      // Vrifier chaque terme de la requte dans le titre
      for (const term of queryTerms) {
        if (title.includes(term)) {
          score += 5;
        }
      }
      
      // Vrifier la description
      const description = (item.description || '').toLowerCase();
      if (description.includes(normalizedQuery)) {
        score += 3;
      }
      
      // Vrifier chaque terme de la requte dans la description
      for (const term of queryTerms) {
        if (description.includes(term)) {
          score += 1;
        }
      }
      
      // Vrifier les genres
      if (item.genres) {
        for (const genre of item.genres) {
          const normalizedGenre = genre.toLowerCase();
          if (normalizedGenre.includes(normalizedQuery) || normalizedQuery.includes(normalizedGenre)) {
            score += 2;
          }
        }
      }
      
      return score;
    };

    // Filtrer les lments avec un score > 0 et trier par score dcroissant
    return this.items
      .map(item => ({ ...item, _score: calculateScore(item) }))
      .filter(item => item._score > 0)
      .sort((a, b) => b._score - a._score);
  }

  /**
   * Rcupre des suggestions pour l'autocompltion
   * @param {String} prefix - Prfixe pour l'autocompltion
   * @param {Number} size - Nombre de suggestions  retourner
   * @returns {Array} Suggestions d'autocompltion
   */
  getSuggestions(prefix, size = 10) {
    if (!prefix || prefix.trim() === '') {
      return [];
    }

    const normalizedPrefix = prefix.toLowerCase().trim();
    
    // Trouver les titres qui commencent par le prfixe
    const exactMatches = this.items
      .filter(item => item.title && item.title.toLowerCase().startsWith(normalizedPrefix))
      .map(item => item.title);
    
    // Trouver les titres qui contiennent le prfixe
    const partialMatches = this.items
      .filter(item => item.title && !item.title.toLowerCase().startsWith(normalizedPrefix) && 
                     item.title.toLowerCase().includes(normalizedPrefix))
      .map(item => item.title);
    
    // Combiner les rsultats et limiter la taille
    return [...new Set([...exactMatches, ...partialMatches])].slice(0, size);
  }

  /**
   * Rcupre les statistiques de l'index
   * @returns {Object} Statistiques de l'index
   */
  getStats() {
    return {
      documentCount: this.items.length,
      lastUpdate: this.lastUpdate
    };
  }
}

// Crer une instance singleton
const inMemoryIndex = new InMemoryIndex();

export { inMemoryIndex };
