import { useState, useEffect } from 'react';

/**
 * Hook personnalis pour dbouncer une valeur
 * Utile pour viter des appels API trop frquents lors de la saisie utilisateur
 * 
 * @param {any} value - La valeur  dbouncer
 * @param {number} delay - Dlai en millisecondes
 * @returns {any} La valeur debounce
 */
export function useDebounce(value, delay) {
  const [debouncedValue, setDebouncedValue] = useState(value);

  useEffect(() => {
    // Dfinir un timer pour mettre  jour la valeur debounce aprs le dlai
    const timer = setTimeout(() => {
      setDebouncedValue(value);
    }, delay);

    // Nettoyer le timer si la valeur change avant la fin du dlai
    return () => {
      clearTimeout(timer);
    };
  }, [value, delay]);

  return debouncedValue;
}
