/**
 * IndexedDBService
 * 
 * Service de gestion de la base de donnes IndexedDB pour le stockage local des mtadonnes.
 * Fournit une interface simple pour les oprations CRUD sur la base de donnes.
 */

class IndexedDBService {
  /**
   * Constructeur
   * @param {String} dbName - Nom de la base de donnes
   * @param {Number} version - Version de la base de donnes
   */
  constructor(dbName, version) {
    this.dbName = dbName;
    this.version = version;
    this.db = null;
    this.isOpening = false;
    this.openPromise = null;
  }

  /**
   * Ouvre la connexion  la base de donnes
   * @param {Array} stores - Configuration des magasins d'objets
   * @returns {Promise<IDBDatabase>} Instance de la base de donnes
   */
  open(stores = []) {
    // Si la base de donnes est dj ouverte, retourner la promesse existante
    if (this.db) {
      return Promise.resolve(this.db);
    }

    // Si une ouverture est dj en cours, retourner la promesse existante
    if (this.isOpening) {
      return this.openPromise;
    }

    this.isOpening = true;
    this.openPromise = new Promise((resolve, reject) => {
      if (!window.indexedDB) {
        this.isOpening = false;
        return reject(new Error('IndexedDB n\'est pas support par ce navigateur.'));
      }

      const request = window.indexedDB.open(this.dbName, this.version);

      request.onerror = (event) => {
        console.error(`[IndexedDBService] Erreur lors de l'ouverture de la base de donnes:`, event.target.error);
        this.isOpening = false;
        reject(event.target.error);
      };

      request.onsuccess = (event) => {
        this.db = event.target.result;
        this.isOpening = false;
        console.log(`[IndexedDBService] Base de donnes ${this.dbName} ouverte avec succs.`);
        resolve(this.db);
      };

      request.onupgradeneeded = (event) => {
        const db = event.target.result;
        console.log(`[IndexedDBService] Mise  niveau de la base de donnes ${this.dbName} vers la version ${this.version}.`);

        // Crer ou mettre  jour les magasins d'objets
        for (const store of stores) {
          let objectStore;

          // Vrifier si le magasin existe dj
          if (!db.objectStoreNames.contains(store.name)) {
            // Crer un nouveau magasin
            objectStore = db.createObjectStore(store.name, { keyPath: store.keyPath });
            console.log(`[IndexedDBService] Magasin d'objets ${store.name} cr.`);
          } else {
            // Utiliser un magasin existant
            objectStore = event.target.transaction.objectStore(store.name);
            console.log(`[IndexedDBService] Magasin d'objets ${store.name} existant.`);
          }

          // Crer les indices si spcifis
          if (store.indices) {
            for (const index of store.indices) {
              // Vrifier si l'indice existe dj
              if (!objectStore.indexNames.contains(index.name)) {
                objectStore.createIndex(index.name, index.keyPath, {
                  unique: !!index.unique,
                  multiEntry: !!index.multiEntry
                });
                console.log(`[IndexedDBService] Indice ${index.name} cr pour ${store.name}.`);
              }
            }
          }
        }
      };
    });

    return this.openPromise;
  }

  /**
   * Ferme la connexion  la base de donnes
   */
  close() {
    if (this.db) {
      this.db.close();
      this.db = null;
      console.log(`[IndexedDBService] Connexion  la base de donnes ${this.dbName} ferme.`);
    }
  }

  /**
   * Effectue une transaction sur un magasin d'objets
   * @param {String} storeName - Nom du magasin d'objets
   * @param {String} mode - Mode de transaction ('readonly' ou 'readwrite')
   * @param {Function} callback - Fonction de rappel avec l'objet de transaction
   * @returns {Promise<any>} Rsultat de la transaction
   */
  async transaction(storeName, mode, callback) {
    await this.open();

    return new Promise((resolve, reject) => {
      if (!this.db.objectStoreNames.contains(storeName)) {
        return reject(new Error(`Le magasin d'objets ${storeName} n'existe pas.`));
      }

      const transaction = this.db.transaction(storeName, mode);
      const store = transaction.objectStore(storeName);

      let result;
      try {
        result = callback(store, transaction);
      } catch (error) {
        reject(error);
      }

      transaction.oncomplete = () => {
        resolve(result);
      };

      transaction.onerror = (event) => {
        console.error(`[IndexedDBService] Erreur de transaction:`, event.target.error);
        reject(event.target.error);
      };

      transaction.onabort = (event) => {
        console.warn(`[IndexedDBService] Transaction annule:`, event.target.error);
        reject(event.target.error || new Error('Transaction annule'));
      };
    });
  }

  /**
   * Ajoute un lment  un magasin d'objets
   * @param {String} storeName - Nom du magasin d'objets
   * @param {Object} item - lment  ajouter
   * @returns {Promise<any>} Cl de l'lment ajout
   */
  async add(storeName, item) {
    return this.transaction(storeName, 'readwrite', (store) => {
      const request = store.add(item);
      return new Promise((resolve, reject) => {
        request.onsuccess = (event) => resolve(event.target.result);
        request.onerror = (event) => reject(event.target.error);
      });
    });
  }

  /**
   * Met  jour un lment dans un magasin d'objets
   * @param {String} storeName - Nom du magasin d'objets
   * @param {Object} item - lment  mettre  jour
   * @returns {Promise<any>} Cl de l'lment mis  jour
   */
  async put(storeName, item) {
    return this.transaction(storeName, 'readwrite', (store) => {
      const request = store.put(item);
      return new Promise((resolve, reject) => {
        request.onsuccess = (event) => resolve(event.target.result);
        request.onerror = (event) => reject(event.target.error);
      });
    });
  }

  /**
   * Ajoute ou met  jour plusieurs lments dans un magasin d'objets
   * @param {String} storeName - Nom du magasin d'objets
   * @param {Array} items - lments  ajouter ou mettre  jour
   * @returns {Promise<Array>} Cls des lments ajouts ou mis  jour
   */
  async bulkPut(storeName, items) {
    if (!items || !items.length) {
      return [];
    }

    return this.transaction(storeName, 'readwrite', (store) => {
      const promises = items.map((item) => {
        return new Promise((resolve, reject) => {
          const request = store.put(item);
          request.onsuccess = (event) => resolve(event.target.result);
          request.onerror = (event) => reject(event.target.error);
        });
      });

      return Promise.all(promises);
    });
  }

  /**
   * Rcupre un lment par sa cl
   * @param {String} storeName - Nom du magasin d'objets
   * @param {any} key - Cl de l'lment
   * @returns {Promise<any>} lment trouv ou undefined
   */
  async get(storeName, key) {
    return this.transaction(storeName, 'readonly', (store) => {
      const request = store.get(key);
      return new Promise((resolve, reject) => {
        request.onsuccess = (event) => resolve(event.target.result);
        request.onerror = (event) => reject(event.target.error);
      });
    });
  }

  /**
   * Rcupre tous les lments d'un magasin d'objets
   * @param {String} storeName - Nom du magasin d'objets
   * @param {IDBKeyRange} query - Requte de plage de cls (optionnel)
   * @param {Object} options - Options supplmentaires
   * @returns {Promise<Array>} Tous les lments
   */
  async getAll(storeName, query = null, options = {}) {
    const { limit, direction = 'next' } = options;

    return this.transaction(storeName, 'readonly', (store) => {
      return new Promise((resolve, reject) => {
        const items = [];
        let cursorRequest;

        if (options.index) {
          const index = store.index(options.index);
          cursorRequest = index.openCursor(query, direction);
        } else {
          cursorRequest = store.openCursor(query, direction);
        }

        cursorRequest.onsuccess = (event) => {
          const cursor = event.target.result;
          if (cursor) {
            items.push(cursor.value);
            if (limit && items.length >= limit) {
              resolve(items);
            } else {
              cursor.continue();
            }
          } else {
            resolve(items);
          }
        };

        cursorRequest.onerror = (event) => reject(event.target.error);
      });
    });
  }

  /**
   * Rcupre tous les lments correspondant  un indice
   * @param {String} storeName - Nom du magasin d'objets
   * @param {String} indexName - Nom de l'indice
   * @param {any} indexValue - Valeur de l'indice
   * @param {Object} options - Options supplmentaires
   * @returns {Promise<Array>} lments correspondants
   */
  async getAllFromIndex(storeName, indexName, indexValue, options = {}) {
    const { limit, direction = 'next' } = options;

    return this.transaction(storeName, 'readonly', (store) => {
      return new Promise((resolve, reject) => {
        if (!store.indexNames.contains(indexName)) {
          return reject(new Error(`L'indice ${indexName} n'existe pas dans ${storeName}.`));
        }

        const index = store.index(indexName);
        const items = [];
        let cursorRequest;

        if (indexValue !== undefined) {
          const keyRange = IDBKeyRange.only(indexValue);
          cursorRequest = index.openCursor(keyRange, direction);
        } else {
          cursorRequest = index.openCursor(null, direction);
        }

        cursorRequest.onsuccess = (event) => {
          const cursor = event.target.result;
          if (cursor) {
            items.push(cursor.value);
            if (limit && items.length >= limit) {
              resolve(items);
            } else {
              cursor.continue();
            }
          } else {
            resolve(items);
          }
        };

        cursorRequest.onerror = (event) => reject(event.target.error);
      });
    });
  }

  /**
   * Supprime un lment par sa cl
   * @param {String} storeName - Nom du magasin d'objets
   * @param {any} key - Cl de l'lment
   * @returns {Promise<void>}
   */
  async delete(storeName, key) {
    return this.transaction(storeName, 'readwrite', (store) => {
      const request = store.delete(key);
      return new Promise((resolve, reject) => {
        request.onsuccess = () => resolve();
        request.onerror = (event) => reject(event.target.error);
      });
    });
  }

  /**
   * Supprime tous les lments d'un magasin d'objets
   * @param {String} storeName - Nom du magasin d'objets
   * @returns {Promise<void>}
   */
  async clear(storeName) {
    return this.transaction(storeName, 'readwrite', (store) => {
      const request = store.clear();
      return new Promise((resolve, reject) => {
        request.onsuccess = () => resolve();
        request.onerror = (event) => reject(event.target.error);
      });
    });
  }

  /**
   * Compte le nombre d'lments dans un magasin d'objets
   * @param {String} storeName - Nom du magasin d'objets
   * @returns {Promise<Number>} Nombre d'lments
   */
  async count(storeName) {
    return this.transaction(storeName, 'readonly', (store) => {
      const request = store.count();
      return new Promise((resolve, reject) => {
        request.onsuccess = (event) => resolve(event.target.result);
        request.onerror = (event) => reject(event.target.error);
      });
    });
  }

  /**
   * Vrifie si un lment existe
   * @param {String} storeName - Nom du magasin d'objets
   * @param {any} key - Cl de l'lment
   * @returns {Promise<Boolean>} True si l'lment existe
   */
  async exists(storeName, key) {
    const item = await this.get(storeName, key);
    return !!item;
  }

  /**
   * Effectue une recherche dans un magasin d'objets
   * @param {String} storeName - Nom du magasin d'objets
   * @param {Function} predicate - Fonction de prdicat pour filtrer les lments
   * @param {Object} options - Options supplmentaires
   * @returns {Promise<Array>} lments correspondants
   */
  async search(storeName, predicate, options = {}) {
    const { limit } = options;
    const allItems = await this.getAll(storeName);
    const filteredItems = allItems.filter(predicate);
    
    if (limit) {
      return filteredItems.slice(0, limit);
    }
    
    return filteredItems;
  }
}

export { IndexedDBService };
export default IndexedDBService;
