/**
 * Configuration de l'API pour les mtadonnes enrichies
 * Ce fichier gre l'accs aux mtadonnes de FloDrama via CloudFront
 * avec intgration Bunny CDN pour les vidos
 */

import { BUNNY_CDN_CONFIG } from '../utils/bunny-cdn-config';
// import { getBunnyVideoUrl } from '../utils/bunny-cdn-integration';

// URLs pour les mtadonnes
const METADATA_URL = 'https://api.flodrama.com/metadata';
const FALLBACK_URL = 'https://d2ra390ol17u3n.cloudfront.net/data/metadata.json';
const LOCAL_FALLBACK_PATH = '/data/metadata.json';

// URL de base pour les assets (posters, bannires, etc.)
export const ASSETS_URL = 'https://flodrama.com/assets';

// URL de base pour les vidos via Bunny CDN
export const VIDEOS_URL = BUNNY_CDN_CONFIG.baseUrl;

/**
 * Rcupre les mtadonnes depuis l'API ou le fallback
 * @returns {Promise<Object>} Les mtadonnes
 */
export const fetchMetadata = async () => {
  try {
    // Utiliser d'abord le fichier local pour le dveloppement
    console.log('Tentative de rcupration des mtadonnes depuis le fichier local...');
    const localResponse = await fetch(LOCAL_FALLBACK_PATH, { 
      headers: { 'Accept': 'application/json' }
    });
    
    if (localResponse.ok) {
      const data = await localResponse.json();
      console.log('Mtadonnes rcupres avec succs depuis le fichier local');
      return data;
    }
    
    console.log('Fichier local non disponible, tentative de rcupration depuis l\'API...');
    const response = await fetch(METADATA_URL, { 
      headers: { 'Accept': 'application/json' },
      timeout: 5000 // Timeout de 5 secondes
    });
    
    if (!response.ok) {
      throw new Error(`Erreur HTTP: ${response.status}`);
    }
    
    const data = await response.json();
    console.log('Mtadonnes rcupres avec succs depuis l\'API');
    return data;
  } catch (error) {
    console.warn(`chec de rcupration depuis l'API: ${error.message}`);
    
    try {
      console.log('Tentative de rcupration depuis CloudFront...');
      const fallbackResponse = await fetch(FALLBACK_URL, { 
        headers: { 'Accept': 'application/json' },
        timeout: 5000
      });
      
      if (!fallbackResponse.ok) {
        throw new Error(`Erreur HTTP fallback: ${fallbackResponse.status}`);
      }
      
      const fallbackData = await fallbackResponse.json();
      console.log('Mtadonnes rcupres avec succs depuis CloudFront');
      return fallbackData;
    } catch (fallbackError) {
      console.warn(`chec de rcupration depuis CloudFront: ${fallbackError.message}`);
      
      try {
        console.log('Tentative de rcupration depuis le fichier local...');
        const localResponse = await fetch(LOCAL_FALLBACK_PATH);
        
        if (!localResponse.ok) {
          throw new Error(`Erreur HTTP local: ${localResponse.status}`);
        }
        
        const localData = await localResponse.json();
        console.log('Mtadonnes rcupres avec succs depuis le fichier local');
        return localData;
      } catch (localError) {
        console.error('Erreur de fallback:', localError);
        
        // Dernier recours : retourner des mtadonnes minimales
        console.warn('Utilisation des mtadonnes de secours minimales');
        return {
          dramas: [],
          movies: [],
          categories: [],
          featured: [],
          trending: [],
          _metadata: {
            version: 'fallback-minimal',
            timestamp: new Date().toISOString(),
            source: 'minimal-fallback'
          }
        };
      }
    }
  }
};

/**
 * Construit l'URL complte pour un asset
 * @param {string} path - Chemin relatif de l'asset
 * @returns {string} URL complte
 */
export const getAssetUrl = (path) => {
  if (!path) return 'https://via.placeholder.com/300x450?text=Image+non+disponible';
  
  // Si le chemin est dj une URL complte, la retourner telle quelle
  if (path.startsWith('http://') || path.startsWith('https://')) {
    return path;
  }
  
  // Nettoyer le chemin pour viter les doubles slashes
  const cleanPath = path.startsWith('/') ? path.substring(1) : path;
  
  // Si le chemin commence dj par 'assets/', ne pas l'ajouter  nouveau
  if (cleanPath.startsWith('assets/')) {
    return `${ASSETS_URL}/${cleanPath.substring(7)}`;
  }
  
  // Construire l'URL complte
  return `${ASSETS_URL}/${cleanPath}`;
};

/**
 * Rcupre l'URL d'une vido avec Bunny CDN
 * @param {Object} item - lment mdia (drama, film, etc.)
 * @param {string} episode - Numro d'pisode (optionnel)
 * @returns {string} URL de la vido
 */
export const getVideoUrl = (item, episode = null) => {
  if (!item) return '';
  
  // Si c'est un film, utiliser directement l'ID du film
  if (item.type === 'movie' || !episode) {
    return `${VIDEOS_URL}/${item.id}/play.mp4`;
  }
  
  // Pour un drama, construire l'URL avec l'pisode
  return `${VIDEOS_URL}/${item.id}/episodes/${episode}/play.mp4`;
};

/**
 * Rcupre les informations d'un pisode
 * @param {string} dramaId - Identifiant du drama
 * @param {number} season - Numro de saison
 * @param {number} episode - Numro d'pisode
 * @param {Object} metadata - Mtadonnes compltes
 * @returns {Object|null} Informations de l'pisode
 */
export const getEpisodeInfo = (dramaId, season, episode, metadata) => {
  try {
    if (!metadata || !metadata.dramas) {
      console.error('Mtadonnes invalides');
      return null;
    }
    
    const drama = metadata.dramas.find(d => d.id === dramaId);
    
    if (!drama) {
      console.warn(`Drama non trouv: ${dramaId}`);
      return null;
    }
    
    const seasonData = drama.seasons.find(s => s.number === parseInt(season));
    
    if (!seasonData) {
      console.warn(`Saison non trouve: ${season} pour ${dramaId}`);
      return null;
    }
    
    const episodeData = seasonData.episodes.find(e => e.number === parseInt(episode));
    
    if (!episodeData) {
      console.warn(`pisode non trouv: ${episode} pour ${dramaId} saison ${season}`);
      return null;
    }
    
    return {
      ...episodeData,
      dramaTitle: drama.title,
      seasonNumber: seasonData.number,
      thumbnail: episodeData.thumbnail || drama.thumbnail,
      videoId: episodeData.videoId || `${dramaId}_s${season}_e${episode}`
    };
  } catch (error) {
    console.error('Erreur lors de la rcupration des informations d\'pisode:', error);
    return null;
  }
};

// Dfinir l'objet  exporter par dfaut
const metadataAPI = {
  fetchMetadata,
  getVideoUrl,
  getEpisodeInfo,
  getAssetUrl
};

export default metadataAPI;
