/**
 * Utilitaires pour la gestion des contenus
 * 
 * Fonctions utilitaires pour la normalisation, la gnration d'identifiants
 * et la dtection de mtadonnes pour les contenus scraps.
 */

/**
 * Gnre un identifiant unique  partir d'un titre
 * @param {String} title - Titre du contenu
 * @returns {String} Identifiant unique
 */
export const generateId = (title) => {
  if (!title) return `unknown-${Date.now()}`;
  
  return title
    .toLowerCase()
    .replace(/[^\w\s-]/g, '') // Supprimer les caractres spciaux
    .replace(/\s+/g, '-')     // Remplacer les espaces par des tirets
    .replace(/-+/g, '-')      // viter les tirets multiples
    .trim();
};

/**
 * Gnre une URL de miniature  partir d'une URL d'image
 * @param {String} imageUrl - URL de l'image originale
 * @returns {String} URL de la miniature
 */
export const generateThumbnailUrl = (imageUrl) => {
  if (!imageUrl) return '';
  
  // Si l'URL contient dj "thumbnail", la retourner telle quelle
  if (imageUrl.includes('thumbnail')) {
    return imageUrl;
  }
  
  // Si c'est une URL Cloudinary, ajouter des paramtres de transformation
  if (imageUrl.includes('cloudinary.com')) {
    return imageUrl.replace('/upload/', '/upload/c_thumb,w_200,g_face/');
  }
  
  // Si c'est une URL d'image standard, la retourner telle quelle
  return imageUrl;
};

/**
 * Dtecte le pays d'origine en fonction des mtadonnes disponibles
 * @param {Object} item - lment de contenu
 * @returns {String} Pays d'origine
 */
export const detectCountry = (item) => {
  // Si le pays est dj spcifi, le retourner
  if (item.country) return item.country;
  
  // Dtecter  partir du type
  if (item.type === 'kshow') return 'Core du Sud';
  
  // Dtecter  partir du titre
  const title = item.title.toLowerCase();
  
  if (title.includes('korean') || 
      title.includes('korea') || 
      title.includes('k-drama')) {
    return 'Core du Sud';
  }
  
  if (title.includes('japanese') || 
      title.includes('japan') || 
      title.includes('j-drama')) {
    return 'Japon';
  }
  
  if (title.includes('chinese') || 
      title.includes('china') || 
      title.includes('c-drama')) {
    return 'Chine';
  }
  
  if (title.includes('thai') || 
      title.includes('thailand') || 
      title.includes('t-drama')) {
    return 'Thalande';
  }
  
  if (title.includes('taiwanese') || 
      title.includes('taiwan')) {
    return 'Tawan';
  }
  
  // Dtecter  partir des genres
  if (item.genres) {
    if (item.genres.includes('Korean Drama')) return 'Core du Sud';
    if (item.genres.includes('Japanese Drama')) return 'Japon';
    if (item.genres.includes('Chinese Drama')) return 'Chine';
    if (item.genres.includes('Thai Drama')) return 'Thalande';
    if (item.genres.includes('Taiwanese Drama')) return 'Tawan';
  }
  
  // Par dfaut
  return 'Inconnu';
};

/**
 * Obtient la priorit d'une source
 * @param {String} source - Nom de la source
 * @returns {Number} Priorit (plus le nombre est bas, plus la priorit est leve)
 */
export const getSourcePriority = (source) => {
  if (!source) return 999;
  
  const sourceLower = source.toLowerCase();
  
  // Priorits des sources (bases sur la fiabilit et la qualit)
  const priorities = {
    'dramacool': 1,
    'voirdrama': 2,
    'dramafast': 3,
    'myasiantv': 4,
    'newasiantv': 5,
    'kissasian': 6,
    'dramago': 7,
    'asianload': 8,
    'viewasian': 9,
    'dramanice': 10
  };
  
  // Rechercher une correspondance partielle
  for (const [key, value] of Object.entries(priorities)) {
    if (sourceLower.includes(key)) {
      return value;
    }
  }
  
  // Priorit par dfaut
  return 100;
};

/**
 * Extrait les mtadonnes d'un lment scrap
 * @param {Object} item - lment scrap
 * @returns {Object} Mtadonnes extraites
 */
export const extractMetadata = (item) => {
  const metadata = {
    episodes: 0,
    year: null,
    status: 'unknown',
    genres: [],
    country: null,
    duration: null,
    rating: null
  };
  
  // Si l'lment n'a pas de mtadonnes, retourner les valeurs par dfaut
  if (!item || !item.meta) {
    return metadata;
  }
  
  // Traiter les mtadonnes sous forme de texte
  if (typeof item.meta === 'string') {
    const metaText = item.meta.toLowerCase();
    
    // Extraire le nombre d'pisodes
    const episodesMatch = metaText.match(/(\d+)\s*ep/i);
    if (episodesMatch) {
      metadata.episodes = parseInt(episodesMatch[1], 10);
    }
    
    // Extraire l'anne
    const yearMatch = metaText.match(/\b(20\d{2}|19\d{2})\b/);
    if (yearMatch) {
      metadata.year = parseInt(yearMatch[1], 10);
    }
    
    // Extraire le statut
    if (metaText.includes('ongoing') || metaText.includes('en cours')) {
      metadata.status = 'ongoing';
    } else if (metaText.includes('completed') || metaText.includes('termin')) {
      metadata.status = 'completed';
    }
    
    // Extraire les genres
    const commonGenres = ['action', 'comedy', 'drama', 'romance', 'thriller', 'horror', 
                          'fantasy', 'sci-fi', 'mystery', 'adventure', 'historical'];
    
    for (const genre of commonGenres) {
      if (metaText.includes(genre)) {
        metadata.genres.push(genre.charAt(0).toUpperCase() + genre.slice(1));
      }
    }
    
    // Extraire le pays
    if (metaText.includes('korean') || metaText.includes('korea')) {
      metadata.country = 'Core du Sud';
    } else if (metaText.includes('japanese') || metaText.includes('japan')) {
      metadata.country = 'Japon';
    } else if (metaText.includes('chinese') || metaText.includes('china')) {
      metadata.country = 'Chine';
    } else if (metaText.includes('thai') || metaText.includes('thailand')) {
      metadata.country = 'Thalande';
    }
  } 
  // Traiter les mtadonnes sous forme d'objet
  else if (typeof item.meta === 'object') {
    if (item.meta.episodes) {
      metadata.episodes = parseInt(item.meta.episodes, 10) || 0;
    }
    
    if (item.meta.year) {
      metadata.year = parseInt(item.meta.year, 10) || null;
    }
    
    if (item.meta.status) {
      metadata.status = item.meta.status;
    }
    
    if (Array.isArray(item.meta.genres)) {
      metadata.genres = item.meta.genres;
    }
    
    if (item.meta.country) {
      metadata.country = item.meta.country;
    }
    
    if (item.meta.duration) {
      metadata.duration = item.meta.duration;
    }
    
    if (item.meta.rating) {
      metadata.rating = parseFloat(item.meta.rating) || null;
    }
  }
  
  return metadata;
};

/**
 * Normalise un titre pour la recherche
 * @param {String} title - Titre  normaliser
 * @returns {String} Titre normalis
 */
export const normalizeTitle = (title) => {
  if (!title) return '';
  
  return title
    .toLowerCase()
    .replace(/[^\w\s]/g, '') // Supprimer les caractres spciaux
    .replace(/\s+/g, ' ')    // Normaliser les espaces
    .trim();
};

/**
 * Calcule la similarit entre deux chanes (distance de Levenshtein simplifie)
 * @param {String} str1 - Premire chane
 * @param {String} str2 - Deuxime chane
 * @returns {Number} Score de similarit (0-1)
 */
export const calculateSimilarity = (str1, str2) => {
  if (!str1 || !str2) return 0;
  
  const s1 = normalizeTitle(str1);
  const s2 = normalizeTitle(str2);
  
  // Si l'une des chanes est vide, retourner 0
  if (!s1 || !s2) return 0;
  
  // Si les chanes sont identiques, retourner 1
  if (s1 === s2) return 1;
  
  // Si l'une des chanes contient l'autre, retourner un score lev
  if (s1.includes(s2) || s2.includes(s1)) {
    return 0.9;
  }
  
  // Calculer la distance de Levenshtein simplifie
  const len1 = s1.length;
  const len2 = s2.length;
  const maxLen = Math.max(len1, len2);
  
  let matches = 0;
  const minLen = Math.min(len1, len2);
  
  for (let i = 0; i < minLen; i++) {
    if (s1[i] === s2[i]) {
      matches++;
    }
  }
  
  return matches / maxLen;
};

/**
 * Trouve la meilleure correspondance pour un titre dans une liste d'lments
 * @param {String} title - Titre  rechercher
 * @param {Array} items - Liste d'lments
 * @param {Number} threshold - Seuil de similarit (0-1)
 * @returns {Object|null} Meilleure correspondance ou null
 */
export const findBestMatch = (title, items, threshold = 0.7) => {
  if (!title || !items || !items.length) return null;
  
  let bestMatch = null;
  let bestScore = 0;
  
  for (const item of items) {
    const score = calculateSimilarity(title, item.title);
    
    if (score > bestScore && score >= threshold) {
      bestScore = score;
      bestMatch = item;
    }
  }
  
  return bestMatch;
};
