/**
 * ProxyService
 * 
 * Service pour contourner les restrictions CORS et les protections anti-bot
 * en utilisant un proxy Puppeteer avanc
 * 
 * NOTE: Ce service est dsactiv en production pour viter les conflits avec Elasticsearch
 */

class ProxyService {
  constructor() {
    // Dterminer l'environnement
    this.isDevelopment = typeof process !== 'undefined' && 
      process.env && 
      process.env.NODE_ENV === 'development';
    
    // Service dsactiv en production et dans le navigateur
    this.isEnabled = this.isDevelopment && typeof window === 'undefined';
    
    // Configuration des URLs de proxy
    this.puppeteerProxyUrl = 'https://proxy.flodrama.org/proxy';
    this.corsAnywhereUrl = 'https://cors-anywhere.herokuapp.com';
    
    // Configuration des timeouts et retries
    this.timeout = 30000;
    this.maxRetries = 3;
    this.retryDelay = 1000;
    
    console.log(`[ProxyService] Initialis en mode ${this.isDevelopment ? 'dveloppement' : 'production'} - Service ${this.isEnabled ? 'activ' : 'dsactiv'}`);
  }
  
  /**
   * Construit l'URL du proxy pour une URL cible
   * @param {String} url - URL cible
   * @param {Boolean} useCorsProxy - Utiliser le proxy CORS Anywhere au lieu du proxy Puppeteer
   * @returns {String} URL du proxy ou URL originale si le service est dsactiv
   */
  getProxyUrl(url, useCorsProxy = false) {
    try {
      // Vrifier que l'URL est dfinie
      if (!url) {
        throw new Error('URL non dfinie');
      }
      
      // Si le service est dsactiv, retourner l'URL originale
      if (!this.isEnabled) {
        return url;
      }
      
      // Encoder l'URL pour viter les problmes avec les caractres spciaux
      const encodedUrl = encodeURIComponent(url);
      
      if (useCorsProxy) {
        // Utiliser CORS Anywhere comme solution de secours
        return `${this.corsAnywhereUrl}/${url}`;
      } else {
        // Utiliser le proxy Puppeteer par dfaut
        return `${this.puppeteerProxyUrl}?url=${encodedUrl}`;
      }
    } catch (error) {
      console.error(`[ProxyService] Erreur lors de la construction de l'URL du proxy:`, error);
      return url; // Retourner l'URL originale en cas d'erreur
    }
  }
  
  /**
   * Effectue une requte via le proxy avec retry automatique
   * @param {String} url - URL  requter
   * @param {Object} options - Options de la requte fetch
   * @param {Boolean} useCorsProxy - Utiliser le proxy CORS
   * @returns {Promise<Response>} Rponse de la requte
   */
  async fetch(url, options = {}, useCorsProxy = false) {
    try {
      // Vrifier que l'URL est dfinie
      if (!url) {
        throw new Error('URL non dfinie');
      }
      
      // Si le service est dsactiv, effectuer la requte directement
      if (!this.isEnabled) {
        return fetch(url, options);
      }
      
      let attempts = 0;
      let lastError = null;
      
      // Boucle de retry
      while (attempts < this.maxRetries) {
        try {
          attempts++;
          
          // Dterminer l'URL du proxy  utiliser
          let targetUrl;
          let fetchOptions;
          
          if (useCorsProxy) {
            // Utiliser CORS Anywhere comme solution de secours
            targetUrl = this.getProxyUrl(url, true);
            fetchOptions = {
              ...options,
              headers: {
                ...options.headers,
                'X-Requested-With': 'XMLHttpRequest'
              }
            };
          } else {
            // Utiliser le proxy Puppeteer par dfaut
            if (options.method === 'POST' && options.body) {
              // Pour les requtes POST, utiliser l'API POST du proxy Puppeteer
              targetUrl = this.puppeteerProxyUrl;
              
              // Prparer les donnes pour le proxy Puppeteer
              const proxyData = {
                url: url,
                headers: options.headers || {},
                timeout: this.timeout
              };
              
              // Si un corps de requte est fourni, l'ajouter aux donnes du proxy
              if (options.body) {
                if (typeof options.body === 'string') {
                  try {
                    proxyData.body = JSON.parse(options.body);
                  } catch (e) {
                    proxyData.body = options.body;
                  }
                } else if (options.body instanceof FormData) {
                  // Convertir FormData en objet
                  const formDataObj = {};
                  for (const [key, value] of options.body.entries()) {
                    formDataObj[key] = value;
                  }
                  proxyData.formData = formDataObj;
                } else {
                  proxyData.body = options.body;
                }
              }
              
              fetchOptions = {
                method: 'POST',
                headers: {
                  'Content-Type': 'application/json'
                },
                body: JSON.stringify(proxyData)
              };
            } else {
              // Pour les requtes GET, utiliser l'API GET du proxy Puppeteer
              targetUrl = this.getProxyUrl(url);
              fetchOptions = {
                method: 'GET'
              };
            }
          }
          
          // Ajouter le timeout
          const controller = new AbortController();
          const timeoutId = setTimeout(() => controller.abort(), this.timeout);
          
          fetchOptions.signal = controller.signal;
          
          // Effectuer la requte
          const response = await fetch(targetUrl, fetchOptions);
          
          // Annuler le timeout
          clearTimeout(timeoutId);
          
          // Vrifier si la rponse est OK
          if (!response.ok) {
            throw new Error(`Rponse HTTP non OK: ${response.status} ${response.statusText}`);
          }
          
          // Si nous utilisons le proxy Puppeteer, nous devons extraire le contenu de la rponse
          if (!useCorsProxy) {
            const responseData = await response.json();
            
            // Crer une nouvelle rponse  partir des donnes du proxy
            const headers = new Headers();
            headers.append('Content-Type', 'text/html; charset=utf-8');
            
            if (responseData.cookies) {
              const cookieHeader = responseData.cookies
                .map(cookie => `${cookie.name}=${cookie.value}`)
                .join('; ');
              
              headers.append('Set-Cookie', cookieHeader);
            }
            
            return new Response(responseData.content, {
              status: 200,
              headers: headers
            });
          }
          
          return response;
        } catch (error) {
          lastError = error;
          console.warn(`[ProxyService] Tentative ${attempts}/${this.maxRetries} choue:`, error);
          
          // Si c'est la dernire tentative, propager l'erreur
          if (attempts >= this.maxRetries) {
            // Si nous avons chou avec le proxy Puppeteer, essayer avec CORS Anywhere
            if (!useCorsProxy) {
              console.log('[ProxyService] chec avec le proxy Puppeteer, tentative avec CORS Anywhere...');
              return this.fetch(url, options, true);
            }
            
            throw error;
          }
          
          // Attendre avant la prochaine tentative
          const currentAttempt = attempts; // Capturer la valeur actuelle de attempts
          await new Promise(resolve => setTimeout(resolve, this.retryDelay * currentAttempt));
        }
      }
      
      // Si toutes les tentatives ont chou
      throw lastError || new Error('chec de la requte aprs plusieurs tentatives');
    } catch (error) {
      console.error(`[ProxyService] Erreur critique:`, error);
      throw error;
    }
  }
  
  /**
   * Effectue une requte GET via le proxy
   * @param {String} url - URL  requter
   * @param {Object} options - Options de la requte fetch
   * @param {Boolean} useCorsProxy - Utiliser le proxy CORS
   * @returns {Promise<Object>} Donnes de la rponse
   */
  async get(url, options = {}, useCorsProxy = false) {
    try {
      // Vrifier que l'URL est dfinie
      if (!url) {
        throw new Error('URL non dfinie');
      }
      
      // Si le service est dsactiv, effectuer la requte directement
      if (!this.isEnabled) {
        const response = await fetch(url, options);
        const contentType = response.headers.get('content-type') || '';
        if (contentType.includes('application/json')) {
          return response.json();
        } else {
          return response.text();
        }
      }
      
      const response = await this.fetch(url, {
        ...options,
        method: 'GET'
      }, useCorsProxy);
      
      // Dterminer le type de rponse
      const contentType = response.headers.get('content-type') || '';
      
      if (contentType.includes('application/json')) {
        return response.json();
      } else {
        return response.text();
      }
    } catch (error) {
      console.error(`[ProxyService] Erreur lors de la requte GET:`, error);
      throw error;
    }
  }
  
  /**
   * Effectue une requte POST via le proxy
   * @param {String} url - URL  requter
   * @param {Object|String} data - Donnes  envoyer
   * @param {Object} options - Options de la requte fetch
   * @param {Boolean} useCorsProxy - Utiliser le proxy CORS
   * @returns {Promise<Object>} Donnes de la rponse
   */
  async post(url, data, options = {}, useCorsProxy = false) {
    try {
      // Vrifier que l'URL est dfinie
      if (!url) {
        throw new Error('URL non dfinie');
      }
      
      // Si le service est dsactiv, effectuer la requte directement
      if (!this.isEnabled) {
        const response = await fetch(url, {
          ...options,
          method: 'POST',
          body: data
        });
        const contentType = response.headers.get('content-type') || '';
        if (contentType.includes('application/json')) {
          return response.json();
        } else {
          return response.text();
        }
      }
      
      // Prparer les donnes
      let body;
      let contentType;
      
      if (typeof data === 'string') {
        body = data;
        contentType = 'text/plain';
      } else if (data instanceof FormData) {
        body = data;
        // Ne pas dfinir Content-Type pour FormData, le navigateur s'en charge
      } else {
        body = JSON.stringify(data);
        contentType = 'application/json';
      }
      
      // Prparer les options
      const postOptions = {
        ...options,
        method: 'POST',
        body,
        headers: {
          ...options.headers
        }
      };
      
      // Ajouter le Content-Type si ncessaire
      if (contentType && !(data instanceof FormData)) {
        postOptions.headers['Content-Type'] = contentType;
      }
      
      const response = await this.fetch(url, postOptions, useCorsProxy);
      
      // Dterminer le type de rponse
      const responseContentType = response.headers.get('content-type') || '';
      
      if (responseContentType.includes('application/json')) {
        return response.json();
      } else {
        return response.text();
      }
    } catch (error) {
      console.error(`[ProxyService] Erreur lors de la requte POST:`, error);
      throw error;
    }
  }
  
  /**
   * Extrait les donnes d'une rponse API qui peut contenir la proprit MAX_LENGTH
   * @param {Object|Array} response - Rponse de l'API
   * @returns {Object} Donnes extraites et proprit MAX_LENGTH
   */
  extractDataFromResponse(response) {
    try {
      // Si la rponse est null ou undefined, retourner un tableau vide
      if (!response) {
        return { data: [], MAX_LENGTH: 0 };
      }
      
      // Si la rponse est dj un tableau, la retourner telle quelle
      if (Array.isArray(response)) {
        return { data: response, MAX_LENGTH: response.length };
      }
      
      // Si la rponse est un objet avec une proprit data et MAX_LENGTH
      if (response && typeof response === 'object' && 'data' in response && 'MAX_LENGTH' in response) {
        return response;
      }
      
      // Si la rponse est un objet mais sans structure spcifique, le retourner tel quel
      return { data: response, MAX_LENGTH: Array.isArray(response) ? response.length : 0 };
    } catch (error) {
      console.error('[ProxyService] Erreur lors de l\'extraction des donnes:', error);
      return { data: [], MAX_LENGTH: 0 };
    }
  }
}

// Crer une instance unique du service
const proxyService = new ProxyService();

// Exporter le service
export default proxyService;
