// Patch pour axios
import './patches/axios-patch.js';

import React from 'react';
import ReactDOM from 'react-dom/client';
import './styles/index.css';
import App from './App';

// Vrifier si nous sommes dans un environnement de production
const isProduction = process.env.NODE_ENV === 'production';

// Point d'entre principal de l'application FloDrama
const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>
);

// En production, nous dsactivons le scraping en arrire-plan pour viter les erreurs
// lies aux modules Node.js dans l'environnement navigateur
if (!isProduction) {
  try {
    // Import dynamique pour viter les erreurs en production
    import('./services/SmartScrapingService.js').then(module => {
      const smartScrapingService = module.default;
      // Dmarrage du scraping quotidien en arrire-plan pour alimenter FloDrama en mtadonnes
      const backgroundScrapingController = smartScrapingService.startDailyBackgroundScraping();

      // Ajouter un gestionnaire d'vnements pour arrter le scraping lors de la fermeture de l'application
      window.addEventListener('beforeunload', () => {
        backgroundScrapingController.stop();
      });

      // couter les vnements de mise  jour des mtadonnes
      window.addEventListener('flodrama:metadata-updated', (event) => {
        console.log('Mtadonnes mises  jour:', event.detail);
        // Vous pouvez ajouter ici une notification ou une mise  jour de l'interface utilisateur
      });
    }).catch(error => {
      console.warn('Le service de scraping n\'a pas pu tre charg:', error.message);
    });
  } catch (error) {
    console.warn('Erreur lors du chargement du service de scraping:', error.message);
  }
} else {
  console.log('Mode production: service de scraping dsactiv pour viter les erreurs de compatibilit navigateur');
}

// Enregistrement du service worker pour le support hors ligne
if ('serviceWorker' in navigator) {
  window.addEventListener('load', () => {
    navigator.serviceWorker.register('/service-worker.js')
      .then(registration => {
        console.log('Service Worker enregistr avec succs:', registration.scope);
      })
      .catch(error => {
        console.error('Erreur lors de l\'enregistrement du Service Worker:', error);
      });
  });
}
