/**
 * Script de surveillance complet pour FloDrama
 * Vrifie l'tat de l'application sur tous les environnements
 * (Amplify, CloudFront, Production)
 */

// Configuration des environnements
const ENVIRONMENTS = {
  production: {
    name: 'Production',
    baseUrl: 'https://flodrama.com',
    apiUrl: 'https://api.flodrama.com',
    cdnUrl: 'https://videos.flodrama.com',
    fallbackUrl: 'https://d2ra390ol17u3n.cloudfront.net'
  },
  cloudfront: {
    name: 'CloudFront',
    baseUrl: 'https://d2ra390ol17u3n.cloudfront.net',
    apiUrl: null,
    cdnUrl: 'https://d2ra390ol17u3n.cloudfront.net',
    fallbackUrl: null
  },
  amplify: {
    name: 'Amplify',
    baseUrl: 'https://main.d3f4g5h6j7k8.amplifyapp.com',
    apiUrl: null,
    cdnUrl: 'https://videos.flodrama.com',
    fallbackUrl: 'https://d2ra390ol17u3n.cloudfront.net'
  },
  local: {
    name: 'Local',
    baseUrl: 'http://localhost:3000',
    apiUrl: 'http://localhost:3001',
    cdnUrl: 'https://videos.flodrama.com',
    fallbackUrl: 'https://d2ra390ol17u3n.cloudfront.net'
  }
};

// Fichiers critiques  vrifier
const CRITICAL_FILES = [
  '/static/js/main.js',
  '/static/css/main.css',
  '/fallback/metadata.json',
  '/manifest.json',
  '/favicon.ico'
];

// Seuils de performance (en ms)
const PERFORMANCE_THRESHOLDS = {
  excellent: 500,
  good: 1000,
  acceptable: 2000,
  poor: 3000
};

// Options de configuration
let CONFIG = {
  notify: false,
  verbose: false,
  logToConsole: true,
  logToFile: false,
  logFilePath: './flodrama-monitor.log',
  checkInterval: 300000, // 5 minutes
  environments: ['production', 'cloudfront', 'amplify'],
  alertThreshold: 'poor'
};

/**
 * Vrifie l'accessibilit d'un fichier
 * @param {string} url - URL du fichier  vrifier
 * @param {number} timeout - Dlai maximum d'attente en ms
 * @returns {Promise<Object>} Rsultat de la vrification
 */
const checkFileAccessibility = async (url, timeout = 5000) => {
  const startTime = performance.now();
  
  try {
    const controller = new AbortController();
    const timeoutId = setTimeout(() => controller.abort(), timeout);
    
    const response = await fetch(url, {
      method: 'HEAD',
      signal: controller.signal,
      cache: 'no-store'
    });
    
    clearTimeout(timeoutId);
    
    const endTime = performance.now();
    const loadTime = endTime - startTime;
    
    return {
      url,
      accessible: response.ok,
      status: response.status,
      statusText: response.statusText,
      contentType: response.headers.get('content-type'),
      loadTime,
      performanceRating: getPerformanceRating(loadTime)
    };
  } catch (error) {
    const endTime = performance.now();
    const loadTime = endTime - startTime;
    
    return {
      url,
      accessible: false,
      error: error.name === 'AbortError' ? 'Timeout' : error.message,
      loadTime,
      performanceRating: 'critical'
    };
  }
};

/**
 * Vrifie le type MIME d'un fichier JavaScript
 * @param {string} url - URL du fichier  vrifier
 * @returns {Promise<Object>} Rsultat de la vrification
 */
const checkJavaScriptMimeType = async (url) => {
  try {
    const response = await fetch(url, {
      method: 'HEAD',
      cache: 'no-store'
    });
    
    const contentType = response.headers.get('content-type');
    const isCorrectMimeType = contentType && (
      contentType.includes('application/javascript') || 
      contentType.includes('text/javascript')
    );
    
    return {
      url,
      mimeTypeCorrect: isCorrectMimeType,
      actualMimeType: contentType
    };
  } catch (error) {
    return {
      url,
      mimeTypeCorrect: false,
      error: error.message
    };
  }
};

/**
 * Vrifie un endpoint API
 * @param {string} url - URL de l'endpoint API
 * @returns {Promise<Object>} Rsultat de la vrification
 */
const checkApiEndpoint = async (url) => {
  const startTime = performance.now();
  
  try {
    const response = await fetch(url, {
      method: 'GET',
      headers: {
        'Accept': 'application/json'
      },
      cache: 'no-store'
    });
    
    const endTime = performance.now();
    const loadTime = endTime - startTime;
    
    let isValidJson = false;
    
    try {
      await response.json();
      isValidJson = true;
    } catch (jsonError) {
      isValidJson = false;
    }
    
    return {
      url,
      accessible: response.ok,
      status: response.status,
      statusText: response.statusText,
      isValidJson,
      loadTime,
      performanceRating: getPerformanceRating(loadTime)
    };
  } catch (error) {
    const endTime = performance.now();
    const loadTime = endTime - startTime;
    
    return {
      url,
      accessible: false,
      error: error.message,
      loadTime,
      performanceRating: 'critical'
    };
  }
};

/**
 * value la performance en fonction du temps de chargement
 * @param {number} loadTime - Temps de chargement en ms
 * @returns {string} valuation de la performance
 */
const getPerformanceRating = (loadTime) => {
  if (loadTime <= PERFORMANCE_THRESHOLDS.excellent) {
    return 'excellent';
  } else if (loadTime <= PERFORMANCE_THRESHOLDS.good) {
    return 'good';
  } else if (loadTime <= PERFORMANCE_THRESHOLDS.acceptable) {
    return 'acceptable';
  } else if (loadTime <= PERFORMANCE_THRESHOLDS.poor) {
    return 'poor';
  } else {
    return 'critical';
  }
};

/**
 * Vrifie l'tat d'un environnement
 * @param {string} envKey - Cl de l'environnement
 * @returns {Promise<Object>} Rapport de l'environnement
 */
const checkEnvironment = async (envKey) => {
  const env = ENVIRONMENTS[envKey];
  
  if (!env) {
    return {
      name: envKey,
      error: 'Environnement non configur'
    };
  }
  
  const results = {
    name: env.name,
    baseUrl: env.baseUrl,
    timestamp: new Date().toISOString(),
    files: [],
    api: null,
    cdn: null,
    overall: 'unknown'
  };
  
  // Vrifier les fichiers critiques
  for (const file of CRITICAL_FILES) {
    const fileUrl = `${env.baseUrl}${file}`;
    const fileCheck = await checkFileAccessibility(fileUrl);
    
    // Vrifier le type MIME pour les fichiers JavaScript
    if (file.endsWith('.js') && fileCheck.accessible) {
      const mimeCheck = await checkJavaScriptMimeType(fileUrl);
      fileCheck.mimeTypeCorrect = mimeCheck.mimeTypeCorrect;
      fileCheck.actualMimeType = mimeCheck.actualMimeType;
    }
    
    results.files.push({
      path: file,
      ...fileCheck
    });
  }
  
  // Vrifier l'API (uniquement en production)
  if (env.apiUrl) {
    const apiCheck = await checkApiEndpoint(`${env.apiUrl}/status`);
    results.api = apiCheck;
  }
  
  // Vrifier le CDN
  if (env.cdnUrl) {
    const cdnCheck = await checkFileAccessibility(`${env.cdnUrl}/health-check.txt`);
    results.cdn = cdnCheck;
  }
  
  // valuer l'tat global
  results.overall = evaluateOverallStatus(results);
  
  return results;
};

/**
 * value l'tat global d'un environnement
 * @param {Object} envResults - Rsultats des vrifications
 * @returns {string} tat global
 */
const evaluateOverallStatus = (envResults) => {
  // Vrifier si des fichiers critiques sont inaccessibles
  const criticalFilesMissing = envResults.files.some(file => 
    !file.accessible && file.path.includes('main.js')
  );
  
  if (criticalFilesMissing) {
    return 'critical';
  }
  
  // Vrifier l'API (si applicable)
  if (envResults.api && !envResults.api.accessible) {
    return 'critical';
  }
  
  // Vrifier le CDN (si applicable)
  if (envResults.cdn && !envResults.cdn.accessible) {
    return 'warning';
  }
  
  // Compter les problmes de performance
  const performanceIssues = envResults.files.filter(file => 
    file.performanceRating === 'poor' || file.performanceRating === 'critical'
  ).length;
  
  if (performanceIssues > 2) {
    return 'warning';
  }
  
  // Vrifier les types MIME
  const mimeTypeIssues = envResults.files.some(file => 
    file.path.endsWith('.js') && file.accessible && !file.mimeTypeCorrect
  );
  
  if (mimeTypeIssues) {
    return 'warning';
  }
  
  return 'healthy';
};

/**
 * Gnre un rapport dtaill
 * @param {Array<Object>} results - Rsultats des vrifications
 * @returns {Object} Rapport dtaill
 */
const generateReport = (results) => {
  const timestamp = new Date().toISOString();
  
  const report = {
    timestamp,
    environments: results,
    summary: {
      healthy: results.filter(env => env.overall === 'healthy').length,
      warning: results.filter(env => env.overall === 'warning').length,
      critical: results.filter(env => env.overall === 'critical').length,
      unknown: results.filter(env => env.overall === 'unknown').length
    },
    alerts: []
  };
  
  // Gnrer les alertes
  results.forEach(env => {
    if (env.overall === 'critical') {
      report.alerts.push({
        severity: 'critical',
        environment: env.name,
        message: `L'environnement ${env.name} est dans un tat critique.`
      });
    } else if (env.overall === 'warning') {
      report.alerts.push({
        severity: 'warning',
        environment: env.name,
        message: `L'environnement ${env.name} prsente des avertissements.`
      });
    }
    
    // Alertes spcifiques pour les fichiers
    env.files.forEach(file => {
      if (!file.accessible) {
        report.alerts.push({
          severity: 'critical',
          environment: env.name,
          message: `Le fichier ${file.path} est inaccessible dans l'environnement ${env.name}.`
        });
      } else if (file.path.endsWith('.js') && !file.mimeTypeCorrect) {
        report.alerts.push({
          severity: 'warning',
          environment: env.name,
          message: `Le fichier ${file.path} a un type MIME incorrect: ${file.actualMimeType}.`
        });
      }
    });
    
    // Alertes pour l'API
    if (env.api && !env.api.accessible) {
      report.alerts.push({
        severity: 'critical',
        environment: env.name,
        message: `L'API est inaccessible dans l'environnement ${env.name}.`
      });
    }
    
    // Alertes pour le CDN
    if (env.cdn && !env.cdn.accessible) {
      report.alerts.push({
        severity: 'warning',
        environment: env.name,
        message: `Le CDN est inaccessible dans l'environnement ${env.name}.`
      });
    }
  });
  
  return report;
};

/**
 * Journalise un message
 * @param {string} message - Message  journaliser
 * @param {string} level - Niveau de journalisation
 */
const log = (message, level = 'info') => {
  const timestamp = new Date().toISOString();
  const formattedMessage = `[${timestamp}] [${level.toUpperCase()}] ${message}`;
  
  if (CONFIG.logToConsole) {
    switch (level) {
      case 'error':
        console.error(formattedMessage);
        break;
      case 'warning':
        console.warn(formattedMessage);
        break;
      case 'info':
      default:
        console.log(formattedMessage);
        break;
    }
  }
  
  // En production, implmenter la journalisation dans un fichier
  if (CONFIG.logToFile) {
    // Code pour crire dans un fichier de log
  }
};

/**
 * Excute la surveillance complte
 * @param {Object} options - Options de configuration
 * @returns {Promise<Object>} Rapport de surveillance
 */
export const runMonitoring = async (options = {}) => {
  // Fusionner les options avec la configuration par dfaut
  CONFIG = { ...CONFIG, ...options };
  
  log('Dmarrage de la surveillance FloDrama');
  
  const results = [];
  
  // Vrifier chaque environnement configur
  for (const envKey of CONFIG.environments) {
    log(`Vrification de l'environnement: ${envKey}`);
    
    try {
      const envResults = await checkEnvironment(envKey);
      results.push(envResults);
      
      log(`tat de l'environnement ${envKey}: ${envResults.overall}`);
      
      if (CONFIG.verbose) {
        log(`Dtails pour ${envKey}: ${JSON.stringify(envResults, null, 2)}`);
      }
    } catch (error) {
      log(`Erreur lors de la vrification de l'environnement ${envKey}: ${error.message}`, 'error');
      
      results.push({
        name: ENVIRONMENTS[envKey]?.name || envKey,
        error: error.message,
        overall: 'unknown'
      });
    }
  }
  
  // Gnrer le rapport
  const report = generateReport(results);
  
  // Afficher les alertes
  if (report.alerts.length > 0) {
    log(`${report.alerts.length} alertes dtectes:`, 'warning');
    
    report.alerts.forEach(alert => {
      log(`[${alert.severity.toUpperCase()}] ${alert.message}`, alert.severity === 'critical' ? 'error' : 'warning');
    });
  } else {
    log('Aucune alerte dtecte.');
  }
  
  // Enregistrer le rapport si demand
  if (CONFIG.notify) {
    log('Enregistrement du rapport...');
    // Code pour enregistrer le rapport (par exemple, dans un fichier JSON)
  }
  
  log('Surveillance termine.');
  
  return report;
};

/**
 * Initialise la surveillance priodique
 * @param {Object} options - Options de configuration
 * @returns {Object} Contrleur de surveillance
 */
export const initMonitoring = (options = {}) => {
  // Fusionner les options avec la configuration par dfaut
  CONFIG = { ...CONFIG, ...options };
  
  let intervalId = null;
  
  const start = () => {
    log('Dmarrage de la surveillance priodique');
    
    // Excuter immdiatement
    runMonitoring(CONFIG);
    
    // Configurer l'intervalle
    intervalId = setInterval(() => {
      runMonitoring(CONFIG);
    }, CONFIG.checkInterval);
    
    return true;
  };
  
  const stop = () => {
    if (intervalId) {
      clearInterval(intervalId);
      intervalId = null;
      log('Surveillance priodique arrte');
      return true;
    }
    
    return false;
  };
  
  return {
    start,
    stop,
    runNow: () => runMonitoring(CONFIG),
    isRunning: () => intervalId !== null,
    updateConfig: (newOptions) => {
      const wasRunning = intervalId !== null;
      
      // Arrter si en cours d'excution
      if (wasRunning) {
        stop();
      }
      
      // Mettre  jour la configuration
      CONFIG = { ...CONFIG, ...newOptions };
      
      // Redmarrer si ncessaire
      if (wasRunning) {
        start();
      }
      
      return CONFIG;
    }
  };
};

// Dfinir l'objet  exporter par dfaut
const monitorFlodrama = {
  runMonitoring,
  initMonitoring,
  ENVIRONMENTS,
  PERFORMANCE_THRESHOLDS
};

export default monitorFlodrama;
