/**
 * Configuration de Bunny CDN pour FloDrama
 * Paramtres de connexion et fonctions utilitaires
 */

// Paramtres de base
export const BUNNY_CDN_CONFIG = {
  // Pull Zone ID: 3467614 (flodrama-videos)
  pullZoneId: 3467614,
  // Hostname personnalis
  hostname: 'videos.flodrama.com',
  // URL de base pour les requtes
  baseUrl: 'https://videos.flodrama.com',
  // URL de fallback (CloudFront)
  fallbackUrl: 'https://d2ra390ol17u3n.cloudfront.net',
  // Paramtres de scurit
  security: {
    // Activation de la protection par token
    tokenProtection: true,
    // Nom du paramtre de token
    tokenName: 'token',
    // Dure de validit du token (en secondes)
    tokenExpiration: 3600
  },
  // Qualits vido disponibles
  qualities: [
    { name: '1080p', width: 1920, height: 1080, bitrate: '5000k' },
    { name: '720p', width: 1280, height: 720, bitrate: '2500k' },
    { name: '480p', width: 854, height: 480, bitrate: '1000k' },
    { name: '360p', width: 640, height: 360, bitrate: '500k' }
  ],
  // Paramtres de performance
  performance: {
    // Dlai avant de basculer vers le fallback (en ms)
    fallbackTimeout: 5000,
    // Nombre maximal de tentatives
    maxRetries: 3,
    // Dlai entre les tentatives (en ms)
    retryDelay: 1000
  }
};

/**
 * Obtient la qualit vido optimale en fonction de la connexion
 * @returns {string} Nom de la qualit optimale
 */
export const getOptimalQuality = () => {
  // Dtection de la connexion rseau
  const connection = navigator.connection || 
                    navigator.mozConnection || 
                    navigator.webkitConnection;
  
  if (connection) {
    // Adapter la qualit en fonction du type de connexion
    const { effectiveType, downlink } = connection;
    
    if (effectiveType === '4g' && downlink > 5) {
      return '1080p';
    } else if (effectiveType === '4g' || (effectiveType === '3g' && downlink > 2)) {
      return '720p';
    } else if (effectiveType === '3g' || (effectiveType === '2g' && downlink > 0.5)) {
      return '480p';
    } else {
      return '360p';
    }
  }
  
  // Par dfaut, retourner 720p
  return '720p';
};

/**
 * Vrifie si le navigateur est compatible avec les fonctionnalits requises
 * @returns {boolean} Compatibilit du navigateur
 */
export const checkBrowserCompatibility = () => {
  // Vrifier la prise en charge de Fetch API
  const hasFetch = 'fetch' in window;
  
  // Vrifier la prise en charge des Promises
  const hasPromise = 'Promise' in window;
  
  // Vrifier la prise en charge des AbortController
  const hasAbortController = 'AbortController' in window;
  
  // Vrifier la prise en charge des vidos HTML5
  const hasVideo = !!document.createElement('video').canPlayType;
  
  return hasFetch && hasPromise && hasAbortController && hasVideo;
};

/**
 * Gnre une URL avec les paramtres de qualit
 * @param {string} baseUrl - URL de base
 * @param {string} quality - Qualit vido
 * @returns {string} URL complte
 */
export const generateQualityUrl = (baseUrl, quality = '720p') => {
  // Vrifier que la qualit demande existe
  const qualityConfig = BUNNY_CDN_CONFIG.qualities.find(q => q.name === quality);
  
  // Si la qualit n'existe pas, utiliser 720p par dfaut
  const selectedQuality = qualityConfig || BUNNY_CDN_CONFIG.qualities.find(q => q.name === '720p');
  
  // Construire l'URL avec les paramtres de qualit
  return `${baseUrl}?quality=${selectedQuality.name}`;
};

// Dfinir l'objet  exporter par dfaut
const bunnyCdnConfig = {
  BUNNY_CDN_CONFIG,
  getOptimalQuality,
  checkBrowserCompatibility,
  generateQualityUrl
};

export default bunnyCdnConfig;
