/**
 * SubscriptionService
 * 
 * Service de gestion des abonnements et du modle freemium de FloDrama.
 * Gre les priodes d'essai, les abonnements et les paiements.
 */

import { createContext, useContext } from 'react';

// Types d'abonnement
export const SUBSCRIPTION_PLANS = {
  ESSENTIAL: {
    id: 'essential',
    name: 'Essentiel',
    description: 'L\'essentiel pour dcouvrir FloDrama',
    monthlyPrice: 1.99,
    yearlyPrice: 23,
    features: [
      'Accs  tous les dramas',
      'Qualit HD',
      'Visionnage sur 1 cran  la fois'
    ]
  },
  PREMIUM: {
    id: 'premium',
    name: 'Premium',
    description: 'Notre offre la plus populaire',
    monthlyPrice: 2.99,
    yearlyPrice: 35,
    features: [
      'Accs  tous les dramas',
      'Qualit Full HD',
      'Visionnage sur 2 crans  la fois',
      'Sans publicit'
    ]
  },
  ULTIMATE: {
    id: 'ultimate',
    name: 'Ultimate',
    description: 'L\'exprience FloDrama ultime',
    monthlyPrice: 4.99,
    yearlyPrice: 55,
    features: [
      'Accs  tous les dramas',
      'Qualit 4K Ultra HD',
      'Visionnage sur 4 crans  la fois',
      'Sans publicit',
      'Accs anticip aux nouveauts'
    ]
  }
};

// Statuts d'abonnement
export const SUBSCRIPTION_STATUS = {
  INACTIVE: 'inactive',           // Pas d'abonnement
  TRIAL: 'trial',                 // Priode d'essai initiale (7 jours)
  SUBSCRIBED_TRIAL: 'subscribed_trial', // Priode d'essai aprs abonnement (1 mois)
  ACTIVE: 'active'                // Abonnement actif et payant
};

class SubscriptionService {
  constructor() {
    // Initialiser le stockage local si ncessaire
    this.initLocalStorage();
  }

  /**
   * Initialise le stockage local pour les donnes d'abonnement
   */
  initLocalStorage() {
    if (!localStorage.getItem('flodrama_subscription')) {
      localStorage.setItem('flodrama_subscription', JSON.stringify({
        status: SUBSCRIPTION_STATUS.INACTIVE,
        plan: null,
        startDate: null,
        endDate: null,
        trialEndDate: null,
        subscriptionTrialEndDate: null,
        paymentMethod: null,
        autoRenew: true
      }));
    }
  }

  /**
   * Rcupre les donnes d'abonnement de l'utilisateur
   * @returns {Object} Donnes d'abonnement
   */
  getSubscriptionData() {
    const data = localStorage.getItem('flodrama_subscription');
    return data ? JSON.parse(data) : null;
  }

  /**
   * Met  jour les donnes d'abonnement
   * @param {Object} data - Nouvelles donnes d'abonnement
   */
  updateSubscriptionData(data) {
    const currentData = this.getSubscriptionData();
    localStorage.setItem('flodrama_subscription', JSON.stringify({
      ...currentData,
      ...data
    }));
  }

  /**
   * Vrifie si l'utilisateur est en priode d'essai initiale
   * @returns {Boolean} Vrai si l'utilisateur est en priode d'essai
   */
  isInInitialTrial() {
    const data = this.getSubscriptionData();
    if (!data) return false;
    
    return data.status === SUBSCRIPTION_STATUS.TRIAL && 
           data.trialEndDate && 
           new Date(data.trialEndDate) > new Date();
  }

  /**
   * Vrifie si l'utilisateur est en priode d'essai aprs abonnement
   * @returns {Boolean} Vrai si l'utilisateur est en priode d'essai aprs abonnement
   */
  isInSubscriptionTrial() {
    const data = this.getSubscriptionData();
    if (!data) return false;
    
    return data.status === SUBSCRIPTION_STATUS.SUBSCRIBED_TRIAL && 
           data.subscriptionTrialEndDate && 
           new Date(data.subscriptionTrialEndDate) > new Date();
  }

  /**
   * Vrifie si l'utilisateur a un abonnement actif
   * @returns {Boolean} Vrai si l'utilisateur a un abonnement actif
   */
  hasActiveSubscription() {
    const data = this.getSubscriptionData();
    if (!data) return false;
    
    return (data.status === SUBSCRIPTION_STATUS.ACTIVE || 
            this.isInInitialTrial() || 
            this.isInSubscriptionTrial()) && 
           data.endDate && 
           new Date(data.endDate) > new Date();
  }

  /**
   * Vrifie si l'utilisateur peut accder au contenu premium
   * @returns {Boolean} Vrai si l'utilisateur peut accder au contenu premium
   */
  canAccessPremiumContent() {
    return this.hasActiveSubscription() || this.isInInitialTrial() || this.isInSubscriptionTrial();
  }

  /**
   * Dmarre la priode d'essai initiale (7 jours)
   * @returns {Object} Donnes d'abonnement mises  jour
   */
  startInitialTrial() {
    const now = new Date();
    const trialEndDate = new Date(now);
    trialEndDate.setDate(trialEndDate.getDate() + 7); // 7 jours d'essai
    
    const data = {
      status: SUBSCRIPTION_STATUS.TRIAL,
      startDate: now.toISOString(),
      trialEndDate: trialEndDate.toISOString(),
      endDate: trialEndDate.toISOString()
    };
    
    this.updateSubscriptionData(data);
    return this.getSubscriptionData();
  }

  /**
   * Souscrit  un abonnement et dmarre la priode d'essai d'un mois
   * @param {String} planId - ID du plan d'abonnement
   * @param {Object} paymentMethod - Mthode de paiement
   * @returns {Object} Donnes d'abonnement mises  jour
   */
  subscribe(planId, paymentMethod) {
    const plan = Object.values(SUBSCRIPTION_PLANS).find(p => p.id === planId);
    if (!plan) {
      throw new Error(`Plan d'abonnement invalide: ${planId}`);
    }
    
    const now = new Date();
    const subscriptionTrialEndDate = new Date(now);
    subscriptionTrialEndDate.setMonth(subscriptionTrialEndDate.getMonth() + 1); // 1 mois d'essai aprs abonnement
    
    const endDate = new Date(subscriptionTrialEndDate);
    endDate.setFullYear(endDate.getFullYear() + 1); // Abonnement d'un an par dfaut
    
    const data = {
      status: SUBSCRIPTION_STATUS.SUBSCRIBED_TRIAL,
      plan: plan,
      startDate: now.toISOString(),
      subscriptionTrialEndDate: subscriptionTrialEndDate.toISOString(),
      endDate: endDate.toISOString(),
      paymentMethod: paymentMethod,
      autoRenew: true
    };
    
    this.updateSubscriptionData(data);
    return this.getSubscriptionData();
  }

  /**
   * Annule l'abonnement (dsactive le renouvellement automatique)
   * @returns {Object} Donnes d'abonnement mises  jour
   */
  cancelSubscription() {
    this.updateSubscriptionData({
      autoRenew: false
    });
    return this.getSubscriptionData();
  }

  /**
   * Ractive l'abonnement (active le renouvellement automatique)
   * @returns {Object} Donnes d'abonnement mises  jour
   */
  reactivateSubscription() {
    this.updateSubscriptionData({
      autoRenew: true
    });
    return this.getSubscriptionData();
  }

  /**
   * Change le plan d'abonnement
   * @param {String} newPlanId - ID du nouveau plan
   * @returns {Object} Donnes d'abonnement mises  jour
   */
  changePlan(newPlanId) {
    const newPlan = Object.values(SUBSCRIPTION_PLANS).find(p => p.id === newPlanId);
    if (!newPlan) {
      throw new Error(`Plan d'abonnement invalide: ${newPlanId}`);
    }
    
    this.updateSubscriptionData({
      plan: newPlan
    });
    return this.getSubscriptionData();
  }

  /**
   * Simule le passage de la priode d'essai  l'abonnement actif
   * @returns {Object} Donnes d'abonnement mises  jour
   */
  activateSubscription() {
    this.updateSubscriptionData({
      status: SUBSCRIPTION_STATUS.ACTIVE
    });
    return this.getSubscriptionData();
  }

  /**
   * Vrifie si l'abonnement a expir et met  jour le statut
   * @returns {Boolean} Vrai si l'abonnement est expir
   */
  checkSubscriptionExpiration() {
    const data = this.getSubscriptionData();
    if (!data) return true;
    
    const now = new Date();
    
    // Vrifier si la priode d'essai initiale est termine
    if (data.status === SUBSCRIPTION_STATUS.TRIAL && 
        data.trialEndDate && 
        new Date(data.trialEndDate) < now) {
      this.updateSubscriptionData({
        status: SUBSCRIPTION_STATUS.INACTIVE
      });
      return true;
    }
    
    // Vrifier si la priode d'essai aprs abonnement est termine
    if (data.status === SUBSCRIPTION_STATUS.SUBSCRIBED_TRIAL && 
        data.subscriptionTrialEndDate && 
        new Date(data.subscriptionTrialEndDate) < now) {
      this.updateSubscriptionData({
        status: SUBSCRIPTION_STATUS.ACTIVE
      });
    }
    
    // Vrifier si l'abonnement est expir
    if (data.endDate && new Date(data.endDate) < now) {
      if (data.autoRenew) {
        // Simuler le renouvellement
        const newEndDate = new Date(data.endDate);
        newEndDate.setFullYear(newEndDate.getFullYear() + 1);
        
        this.updateSubscriptionData({
          endDate: newEndDate.toISOString()
        });
        return false;
      } else {
        this.updateSubscriptionData({
          status: SUBSCRIPTION_STATUS.INACTIVE
        });
        return true;
      }
    }
    
    return false;
  }

  /**
   * Rinitialise les donnes d'abonnement (pour les tests)
   */
  resetSubscriptionData() {
    localStorage.removeItem('flodrama_subscription');
    this.initLocalStorage();
  }

  /**
   * Calcule le nombre de jours restants dans la priode d'essai
   * @returns {Number} Nombre de jours restants
   */
  getRemainingTrialDays() {
    const data = this.getSubscriptionData();
    if (!data) return 0;
    
    const now = new Date();
    let endDate = null;
    
    if (this.isInInitialTrial()) {
      endDate = new Date(data.trialEndDate);
    } else if (this.isInSubscriptionTrial()) {
      endDate = new Date(data.subscriptionTrialEndDate);
    } else {
      return 0;
    }
    
    const diffTime = Math.abs(endDate - now);
    const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
    return diffDays;
  }
}

// Crer une instance unique du service
const subscriptionService = new SubscriptionService();

// Crer un contexte React pour le service d'abonnement
export const SubscriptionContext = createContext(null);

// Hook personnalis pour utiliser le service d'abonnement
export const useSubscription = () => {
  const context = useContext(SubscriptionContext);
  if (!context) {
    throw new Error('useSubscription doit tre utilis  l\'intrieur d\'un SubscriptionProvider');
  }
  return context;
};

export default subscriptionService;
